import { useState, useEffect } from "react";
import "../App.css";
import "../css/Seting.css"

function Settings() {
  const [theme, setTheme] = useState(() => {
    // Ly theme t localStorage nu c, mc nh l "light"
    return localStorage.getItem("theme") || "light";
  });

  // p dng theme vo body hoc root element khi theme thay i
  useEffect(() => {
    document.body.className = `${theme}-theme`; // Thm class vo body
    localStorage.setItem("theme", theme); // Lu theme vo localStorage
  }, [theme]);

  const toggleTheme = () => {
    setTheme((prevTheme) => (prevTheme === "light" ? "dark" : "light"));
  };

  return (
    <div className="settings-container">
      <h2>Ci t</h2>
      <label className="theme-toggle">
        Ch  ti:
        <input
          type="checkbox"
          checked={theme === "dark"}
          onChange={toggleTheme}
        />
      </label>
    </div>
  );
}

export default Settings;