import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import "../css/Search.css";

function Search() {
  const [query, setQuery] = useState("");
  const [results, setResults] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  // Hm chun ha chui tm kim
  const normalizeString = (str) => {
    return str
      .toLowerCase()
      .normalize("NFD")
      .replace(/[\u0300-\u036f]/g, "")
      .replace(/\s+/g, " ")
      .trim();
  };

  // Debounce  tr hon tm kim
  useEffect(() => {
    if (!query) {
      setResults([]);
      return;
    }

    const delayDebounce = setTimeout(() => {
      fetchPodcasts();
    }, 500); // Ch 500ms trc khi gi yu cu

    return () => clearTimeout(delayDebounce); // Hy nu query thay i trc khi timeout
  }, [query]);

  // Hm gi API iTunes  tm kim podcast
  const fetchPodcasts = async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await fetch(
        `https://itunes.apple.com/search?term=${encodeURIComponent(query)}&media=podcast&limit=20`
      );
      if (!response.ok) throw new Error("Khng th tm kim podcast");
      const data = await response.json();

      const podcasts = data.results.map((podcast) => ({
        id: podcast.collectionId,
        title: podcast.collectionName,
        author: podcast.artistName,
        image: podcast.artworkUrl100,
      }));

      setResults(podcasts);
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="search-container">
      <h2>Tm Kim Podcast</h2>
      <input
        type="text"
        placeholder="Nhp tiu  (khng cn du)..."
        value={query}
        onChange={(e) => setQuery(e.target.value)}
        className="search-input"
      />
      <div className="search-results">
        {loading && <p className="loading">ang tm kim...</p>}
        {error && <p className="error">{error}</p>}
        {!loading && !error && results.length > 0 ? (
          results.map((podcast) => (
            <div key={podcast.id} className="podcast-item">
              <Link to={`/podcast/${podcast.id}`} className="podcast-link">
                <img src={podcast.image} alt={podcast.title} className="podcast-thumbnail" />
                <div className="podcast-info">
                  <h3>{podcast.title}</h3>
                  <p>{podcast.author}</p>
                </div>
              </Link>
            </div>
          ))
        ) : !loading && !error && query && (
          <p className="no-results">Khng tm thy podcast ph hp.</p>
        )}
      </div>
    </div>
  );
}

export default Search;